@import url(https://fonts.googleapis.com/css2?family=Crimson+Text:wght@400;600&family=Roboto:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto', 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  /* cursor: none; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
::-webkit-selection {
  background: #FFA5AB;
}
::selection {
  background: #FFA5AB;
}

.screenbox {
  min-height: 100vh;
  width: 60vw;
  min-width: 800px;
  margin: auto;
}

.hero-text {
  position: absolute;
  width: 40vw;
  min-width: 700px;
  font-family: "Crimson Text", serif;
  font-size: 80pt;
  text-align: center;
  font-weight: bold;
  line-height: 100px;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hero-text span {
  color: #FFA5AB;
}

.navbox {
  width: 100vw;
  height: 40vh;
  flex-direction: row;
  text-align: center;
  display: block;
  position: fixed;
}

.navbox a img {
  display: inline-block;
  height: 100%;
  margin-left: 20px;
  margin-right: 20px;
}

.arrow-down {
  position: absolute;
  margin-top: 55vh;
  width: 0; 
  height: 0; 
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 15px solid black;
  left: 50%;
  transform: translate(-50%, -50%);
}

.content-box {
  height: 60%;
  margin: auto;
  margin-top: 20%;
}

.subtitle {
  font-weight: 400;
  font-family: "Crimson Text", serif;
  font-size: 50pt;
  width: 100%;
  text-align: center;
  margin-top: 50px;
  margin-bottom: 50px;
}

.about-me-text {
  width: 40%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 300;
  font-size: 20px;
  margin-left: 15%;
}

.about-me-text a {
  color: #FFA5AB;
  text-decoration: none;
}

.about-me-text a:hover {
  text-decoration: underline;
}

.random-image {
  position: absolute;
  width: 20%;
  margin-left: 50vw;
  margin-top: -200px;
  transform: rotate(199deg);
  animation: bob 5s 1s 1;
  animation-iteration-count:infinite;
}

/* Animation Keyframes*/
@keyframes bob {
  0% { margin-top: -220px }
  50% { margin-top: -180px }
  100% { margin-top: -220px }
}

.work-text {
  width: 70%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 300;
  font-size: 20px;
  margin: auto;
}

.work-row {
  height: 45px;
  color: black;
  transition: 0.3s ease;
}

.work-row:hover {
  color: #FFA5AB;
}

.work-row span {
  float: right;
}

.contact-link {
  width: 100%;
  text-align: center;
  transition: 0.5s ease;
}

.contact-link:hover {
  font-size: 30px;
}

.pink_circle {
  position: fixed;
  z-index: -1;
  border: 2px solid #FFA5AB;
  height: 50px;
  width: 50px;
  border-radius: 50px;
}

.work-content-box {
    min-height: 100vh;
    width: 60vw;
    min-width: 800px;
    margin: auto;
}

.back-arrow {
    position: absolute;
    font-size: 15px;
    margin-left: 20vw;
    margin-top: -50px;
}

.back-arrow:hover {
    color: #FFA5AB;
}

.work-title {
    color: #FFA5AB;
    font-size: 50pt;
    font-weight: 400;
    font-family: "Crimson Text", serif;
    margin-top: 20vh;
}

.work-subtitle {
    color: 'black';
    font-size: 20pt;
    font-weight: 600;
    font-family: "Crimson Text", serif;
    margin-left: 10px;
    margin-bottom: 10px;
    z-index: 5;
    width: 50%;
}

.work-body {
    font-family: Roboto, sans-serif;
    margin-left: 12px;
    margin-top: 35px;
    margin-bottom: 100px;
    width: 50%;
}

.main-img {
    position: absolute;
    margin-left: 25vw;
    max-width: 300px;
    border: 1px solid red;
}

.image-grid {
    /* display: table; */
    /* align-content: flex-start; */
    margin-bottom: 100px;
    margin-top: 50px;
}

.image-grid img {
    max-height: 500px;
    max-width: 500px;
    margin-right: 20px;
    margin-bottom: 20px;
    transition: 0.5s ease;
    border: 1px solid black;
}

.image-grid img:hover {
    margin-top: -50px;
    max-height: 550px;
    max-width: 550px;
    margin-right: 20px;
}

.work-contact {
    font-family: Roboto, sans-serif;
    margin-left: 12px;
    color: #FFA5AB;
}

.work-contact p a {
    text-decoration: none;
    color: #FFA5AB;
}

.work-contact p:hover {
    font-weight: bold;
}

.next-button {
    margin-top: 20px;
    margin-bottom: 100px;
    width: 100%;
}

.next-button span {
    float: right;
}
